.MainHeader {
  padding: 1rem;
  position: sticky;
  top: 0;
  z-index: 9;
  background: #fff;
  border-bottom: 1px solid #e2e2e2;
}

.MainHeader__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.MainHeader__right {
  display: flex;
  align-items: center;
}
